<template>
  <div style="position: relative;">
    <!-- <vue-preview  :slides="slide1" @close="handleClose"></vue-preview> -->
    <div
      class="imgtap"
      v-for="(item,index) in imgData"
    >
      <div style="position: relative;" class="buttonclick">
        <el-button round class="rightbtn" @click.stop.prevent="islikes(item.id)">
          <div style="width: 3.75rem;">
            <font-awesome-icon style="color: red;" icon="heart" />
            &nbsp;{{item.likes}}&nbsp;&nbsp;&nbsp;
          </div>
        </el-button>
        <img
          alt
          width="100%"
          :src="item.url"
          class="animated fadeIn"
          preview="1"
          :preview-text="item.txt"
        />
      </div>
      <p>{{item.txt}}</p>
      <!-- 点赞 &nbsp;&nbsp;&nbsp;&nbsp; -->
      <div></div>
    </div>
  </div>
</template>

<script>
import "font-awesome/css/font-awesome.min.css";
const axios = require("axios");
import qs from "qs";

export default {
  name: "hello",
  data() {
    return {
    imgData: [
        {
          url:"",
          txt: "礼赞共和国  智慧新生活    礼赞共和国  智慧新生活",
          datetime: "2019-08-12 24:24:24",
          likes: "0",
          id: 0
        }
      ],
    };
  },
  created(){
    this.reload(5);
    this.timer = setInterval(() => {
      this.reload(5);
    }, 5000);
  },
  mounted() {
    this.$previewRefresh();
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer); // 在Vue实例销毁前，清除我们的定时器
    }
  },
  methods: {
    handleClose() {
      console.log("close event");
    },
    islikes(id) {
      let data = { id: id ,type:5};
      axios
        .post(this.$store.state.url + `/api/addlike`, qs.stringify(data), {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
          }
        })
        .then(res => {  
          this.reload(5);
        });
    },
    
    reload(type) {
      let data = {type:5};
      axios
        .post(this.$store.state.url+"/api/getAll",qs.stringify(data),)
        .then(response => {
          console.log(response);
          // handle success
          this.imgData = response.data.msg;
        })
        .catch(function(error) {
          // handle error
          console.log(error);
        })
        .finally(function() {
          // always executed
        });
    },
  }
};
</script>

<style scoped>
.imgtap p {
  padding: 0.625rem;
  font-size: 0.8125rem;
  text-align: left;
  line-height: 1.25rem;
  position: relative;
}
.rightbtn {
  position: absolute;
  z-index: 9;
  right: -1.25rem;
}
.el-button {
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  cursor: pointer;
  border: none;
  padding: 0.5rem 1.25rem;
}

.buttonclick .el-button:focus,
.el-button:hover {
  color: #fff;
  border-color: #c6e2ff;
  background-color: rgba(0, 0, 0, 0.5);
}
</style>



