import Vue from 'vue'
import Router from 'vue-router'
import Hompage from '@/components/homepage'
import Album from '@/components/album'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hompage',
      component: Hompage
    },
     {
      path: '/album',
      name: 'Album',
      component: Album
    }
  ]
})



