<template>
  <div class="bg animated fadeIn" :style="{backgroundImage: 'url(' +bg + ')'}">

<el-row class='b_button'>
  <el-button round  @click='enter'>点击进入</el-button>
</el-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      list:[
      ],
      bg:require('../assets/bgimg.jpg')
    }
  },
  methods: {
    enter:function (){
        this.$router.push('/album')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .bg{
    /* background: url('../assets/bgimg.jpg') no-repeat top center; */
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 900;
    background-color: #e3e3e3;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 1;
    transition-property: opacity;
  }

  .b_button,.el-row{
    width: 100%;
    position: fixed;
    bottom: 10%;
    text-align: center;
  }
   .b_button button{
     background: rgba(0,0,0,.5);
     color: #fff;
     cursor: pointer;
     line-height: .5;
  }



</style>



